.card {
    background: white;
    border-radius: 12px;
    /* padding: 24px; */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    min-width: 70%;
    max-width: 500px;
    margin: 20px auto;
    padding: 2% 5% 8% 10%;
}

.header {
    color: #1E40AF;
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center;
    font-weight: 600;
}

.field {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 0;
    border-bottom: 1px solid #E5E7EB;
}

.field:last-child {
    border-bottom: none;
}

.label {
    color: #4B5563;
    font-size: 16px;
    font-weight: 500;
}

.value {
    color: #1E40AF;
    font-size: 18px;
    font-weight: 600;
}

@media (max-width: 480px) {
    .card {
        margin: 10px;
        padding: 16px;
    }
    
    .header {
        font-size: 20px;
    }
    
    .value {
        font-size: 16px;
    }
}
.buttonContainer {
    display: flex;
    justify-content: space-between;
    gap: 50px;
    margin-top: 5%;
    /* margin-left: 60px; */
    padding-top: 2%;
    padding-bottom: 2%;
    border-top: 1px solid #E5E7EB;
    /* border:1px solid black; */
    width: 80%;
    margin-left:10% ;
}

.button {
    flex: 1;
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    font-weight: 600;
    cursor: pointer;
    transition: opacity 0.2s ease;
    font-size: 100%;
    /* width: 100px; */
}

.button:hover {
    opacity: 0.9;
}

.deposit {
    background-color: #10B981;
    color: white;
}

.withdraw {
    background-color: #EF4444;
    color: white;
}

@media (max-width: 480px) {
    .button {
        padding: 8px 16px;
        font-size: 14px;
    }
}
/* // ...existing code... */

.container {
    display: flex;
    justify-content: space-between;
    gap: 1%;
    width: 100%;
    margin: 0 0 0 0;
    /* border:1px solid black; */
}

.mainSection {
    margin-top: 2%;
    
    /* height: fit-content; */
    flex: 2;
    /* border:1px solid black; */
}

.statsCard {
    flex: 1;
    background: white;
    border-radius: 12px;
    padding: 2% 5% 5% 5%;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    height: fit-content;
    margin-top: 2%;
    width: 90%;
    /* max-width: 500px; */
}

.positive {
    color: #10B981 !important;
}

.negative {
    color: #EF4444 !important;
}

@media (max-width: 768px) {
    .container {
        flex-direction: column;
    }

    .statsCard {
        max-width: none;
        margin: 20px auto;
        width: 90%;
    }
}


/* // ...existing styles... */

.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal {
    background: white;
    border-radius: 12px;
    padding: 24px;
    width: 100%;
    max-width: 500px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.modalHeader {
    color: #1E40AF;
    font-size: 1.25rem;
    margin-bottom: 16px;
    text-align: center;
}

.modalContent {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.amountInput {
    padding: 12px;
    border: 2px solid #E5E7EB;
    border-radius: 6px;
    font-size: 1rem;
    width: 95%;
}

.amountInput:focus {
    outline: none;
    border-color: #1E40AF;
}

.modalButtons {
    display: flex;
    gap: 12px;
    justify-content: flex-end;
}

.continueButton {
    background-color: #10B981;
    color: white;
    padding: 8px 16px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 600;
}

.cancelButton {
    background-color: #EF4444;
    color: white;
    padding: 8px 16px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 600;
}

.continueButton:hover,
.cancelButton:hover {
    opacity: 0.9;
}

@media (max-width: 480px) {
    .modal {
        width: 95%;
        padding: 16px;
    }
}

/* Pending request css */
.pendingHeader {
    font-size: 1.5rem;
    color: #fffefe;
    /* margin-left: 10%; */
    padding-left: 10%;
    border-left: 4px solid #2bce74;
}

.tableContainer {
    overflow-x: auto;
    margin: 20px 0;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
}

.transactionTable {
    /* width: 600px; */
    border-collapse: collapse;
    background-color: white;
    font-size: 1.5rem;
    /* border:14px solid black; */
}

.transactionTable th,
.transactionTable td {
    padding: 12px 15px;
    text-align: left;
    border-bottom: 1px solid #eee;
}

.transactionTable th {
    background-color: #f8f9fa;
    font-weight: 600;
    color: #495057;
}

.transactionTable tr:hover {
    background-color: #f5f5f5;
}

.pendingStatus {
    background-color: #ffeeba;
    color: #856404;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 0.8rem;
    font-weight: 500;
}

/* Responsive styles */
@media (max-width: 768px) {
    .tableContainer {
        margin: 10px 0;
    }

    .transactionTable {
        font-size: 0.8rem;
        /* width: 900px; */
    }

    .transactionTable th,
    .transactionTable td {
        padding: 8px 10px;
    }

    .pendingHeader {
        font-size: 1.2rem;
    }
}